.ped_container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  width: 100%;
  gap: 2%;
}
.pedc_heading {
  width: 100%;
  color: rgba(18, 18, 18, 0.9);
  font-size: var(--font-16);
  font-weight: var(--font-6);
  margin: 0;
}
.sd_heading {
  color: var(--t-head-color);
  font-size: var(--font-16);
  font-weight: var(--font-5);
  line-height: var(--line-18);
  margin: 0.5rem 0;
}
.sd_tab {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}
.ast_header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
.ast_header > h6 {
  /* flex: 1 1; */
  width: 18.4%;
  color: rgba(18, 18, 18, 0.9);
  font-size: var(--font-16);
  font-weight: var(--font-5);
  text-align: center;
  margin: 0;
  padding-block: 0.3rem;
  border: 1px solid #cacaca;
  border-right-width: 0;
}
.ast_body {
  display: flex;
}
.astb_day {
  /* flex: 0.4 0.4; */
  width: 8%;
}
.astb_day > h6 {
  color: rgba(18, 18, 18, 0.9);
  font-size: var(--font-14);
  font-weight: var(--font-4);
  text-align: center;
  margin: 0;
  padding-block: 0.3rem;
  border: 1px solid #cacaca;
  border-top-width: 0;
  border-right-width: 0;
}
.astb_val {
  /* flex: 1 1; */
  width: 18.4%;
}
.astb_val > h6 {
  color: rgba(18, 18, 18, 0.9);
  font-size: var(--font-14);
  font-weight: var(--font-4);
  text-align: center;
  margin: 0;
  padding-block: 0.3rem;
  border: 1px solid #cacaca;
  border-top-width: 0;
  border-right-width: 0;
}
.as_flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.as_flex > p {
  color: rgba(18, 18, 18, 0.8);
  font-size: var(--font-14);
  font-weight: var(--font-4);
  text-align: center;
  margin: 0;
}
.as_flex > h6 {
  color: rgba(18, 18, 18, 0.9);
  font-size: var(--font-14);
  font-weight: var(--font-5);
  text-align: center;
  margin: 0;
}
